<div class="filter"><div class="title"><span class="span1"></span></div><a-form class="formout" layout="inline"><div class="forms"><a-form-item label=":"><a-input placeholder="" v-model:value="searchForm.mixedName" style="width: 100px"></a-input></a-form-item><a-form-item label=":"><a-input placeholder="/" v-model:value="searchForm.mainTitle"  style="width: 140px"></a-input></a-form-item><a-form-item label=":"><a-select v-model:value="searchForm.statusStr" placeholder="" style="width: 160px" allowClear><a-select-option value=""></a-select-option><a-select-option value=""></a-select-option><a-select-option value=""></a-select-option><a-select-option value=""></a-select-option></a-select></a-form-item><a-form-item label=":"><a-select v-model:value="searchForm.seniorCategory" placeholder="" style="width: 150px" allowClear><a-select-option v-for="item in sort" :value="item.id">{{ item.categoryName }}</a-select-option></a-select></a-form-item><a-form-item label=":" style="width: 50%"><a-range-picker v-model:value="searchForm.time" showTime></a-range-picker></a-form-item></div><a-form-item class="btns"><a-button type="primary" @click="submitForm"></a-button><a-button @click="resetForm"></a-button></a-form-item></a-form></div><div class="list"><a-tabs><a-tab-pane tab=""><a-table :columns="columns" :data-source="data" :pagination="pagination" :loading="queryRecordLoding" :row-selection="{ selectedRowKeys: selectedRowKeys, onChange: onSelectChange }" rowKey="id" @change="pageChange"><template v-slot:equi="{ text, record }">{{ record.duration > 0 && record.appDuration > 0 ? "PC,APP" : record.duration > 0 ? "PC" : record.appDuration > 0 ? "APP" : " " }}</template><template v-slot:badge="{ text, record }"><a-badge v-if="record.abnormalMark == true" color="cyan" text=""></a-badge><a-badge v-else-if="record.studyStatus == 1" color="orange" text=""></a-badge><a-badge v-else-if="record.studyStatus == 0" color="yellow" text=""></a-badge><a-badge v-else-if="record.studyStatus == 2" color="purple" text=""></a-badge></template><template v-slot:action="{ text, record }"><div class="option"><div class="option-container" @click="showViewrecord(record)"><a></a></div><div class="option-container" @click="delrecord(record)"><span></span></div></div></template></a-table></a-tab-pane><template v-slot:tabBarExtraContent><Abnormal></Abnormal></template></a-tabs><div class="table-foot"><div class="all-check btn-other"><a-checkbox :checked="checked.check" @change="onSelectAll"></a-checkbox></div><div class="all-check btn-other"><a-checkbox :checked="checkedother.check" @change="selectOther"></a-checkbox></div><a-select class="more-btn btn-other" placeholder="" style="width: 120px"><a-select-option @click="closeCheckedRecords" value="closeCheckedRecords"></a-select-option></a-select></div><a-modal :visible="viewrecord.show" title="" dialogClass="modal-help" :width="500" :footer="null" @cancel="closeViewrecord"><a-form layout="vertical" :label-col="labelCol"><a-form-item label=":">{{ records.startDate }}</a-form-item><a-form-item label=":">{{ records.startPeriodDate }}</a-form-item><a-form-item label=":">{{ records.doneDate || "" }}</a-form-item><a-form-item label=":"><div class="card"><div class="left"><a-avatar shape="square" :size="74" :src="records.userCover"></a-avatar></div><div class="right"><div class="top"><span class="name">{{ records.username }}</span></div><div class="bottom"><div class="up"><span></span></div><div class="down"><a-progress :percent="records.studyProgress"></a-progress></div></div></div></div></a-form-item></a-form></a-modal></div>